import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

import store from './UI/store/store';
import App from './UI/App';

ReactDOM.render(
    <React.StrictMode>
        <Provider store={store}>
            <App />
        </Provider>
    </React.StrictMode>,
    document.getElementById('root')
);
